import React from 'react';
import './App.css';
import ListItems from './ListItems'
import JSONDATA from './MOCK_DATA.json'



class App extends React.Component {
  
  constructor(props){
    super(props);
    this.state = {
      items:[],
      currentItem:{
        text:'',
        key:''
      },
      searchTerm:'',
      selectTag: ''
    }
    
    this.addItem = this.addItem.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.setUpdate = this.setUpdate.bind(this);
  }
  selectItem(e) {
    
    const v = e.target.value
    console.log(v);
    // this.setState({
    //   currentItem:{
    //     text: v,
    //     key: Date.now()
    //   },  
    // })
    
    const dict={
      text:v,
      key: Date.now()
  }
  console.log(dict)
    const newItem =dict;
    if(newItem.text !==""){
      const items = [...this.state.items, newItem];
    this.setState({
      items: items,
      currentItem:{
        text:'',
        key:''
      },
      searchTerm: ''

    })
    }
}
  addItem(e){
    e.preventDefault();
    const newItem = this.state.currentItem;
    if(newItem.text !==""){
      const items = [...this.state.items, newItem];
    this.setState({
      items: items,
      currentItem:{
        text:'',
        key:''
      },
      searchTerm: ''

    })
    }
  }
  handleInput(e){
    this.setState({
      currentItem:{
        text: e.target.value,
        key: Date.now()
      },
      searchTerm:e.target.value
      
    })
  }
  deleteItem(key){
    const filteredItems= this.state.items.filter(item =>
      item.key!==key);
    this.setState({
      items: filteredItems
    })

  }
//   showButton() {
//     // var div = document.getElementyById('add');
//     // div.style.display = 'block';
//     console.log("button is")
// }
  setUpdate(text,key){
    console.log("items:"+this.state.items);
    const items = this.state.items;
    items.map(item=>{      
      if(item.key===key){
        console.log(item.key +"    "+key)
        item.text= text;
      }
    })
    this.setState({
      items: items
    })
    
   
  }
 render(){
  return (
    <div className="App">
      <header>
        <form id="to-do-form" onSubmit={this.addItem}>
          <input type="text" placeholder="Enter task" value= {this.state.currentItem.text} onChange={this.handleInput}></input>
          <button type="submit" id="add" className="addbutton">Add</button>
        </form>
        {JSONDATA.filter((val)=>{
       if(this.state.searchTerm===""){
         let str="";
         //this.showButton()
         return str
       }
       else if(val.first_name.toLowerCase().includes(this.state.searchTerm.toLowerCase())) 
       {
         return val
       }
    }).map((val,key)=>{
     
      return(
        <div className="user" key={key}> 
      <button type="button" id="btn" value={val.first_name} onClick={e => this.selectItem(e, "value")}>{val.first_name}</button>
        </div>
      )
    })}
        <p>{this.state.items.text}</p>
        
          <ListItems items={this.state.items} deleteItem={this.deleteItem} setUpdate={this.setUpdate}/>
        
      </header>
    </div>
  );
 }
}


export default App;
